@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  color: #333;
  line-height: 1.2;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App{max-width:1000px;padding:5px;margin:0 auto 36px}h2{font-size:28px}.result-table{width:100%;border-collapse:collapse}.result-table tr:nth-child(2n+1){background-color:#eeeeee}.result-table th{padding:8px 6px;background-color:#666;color:#ffffff;font-weight:600;text-align:left}.result-table td{border-bottom:1px solid #bbbbbb;padding:3px 6px}.result-table td a{text-decoration:none}.result-table tr:hover td a{color:#f03}

.header{padding:16px;margin-bottom:36px;display:flex;justify-content:center}.header__navigation{display:flex;justify-content:center;grid-gap:18px;gap:18px;font-size:20px}.navigation{text-decoration:none;border:2px solid transparent;padding:2px}.navigation--active{border-bottom-color:#f03}


.post-wrapper{display:grid;grid-template-columns:1fr;justify-items:left;grid-row-gap:36px;row-gap:36px}

.post__title{margin-bottom:4px}.post__title::first-letter{text-transform:uppercase}.post__userid{color:#999;margin-bottom:16px}.post__content::first-letter{text-transform:uppercase}

.button{padding:8px 16px;font-size:14px;line-height:1;font-weight:500;color:#fff;background-color:#f16767;border:1px solid transparent;border-radius:4px;cursor:pointer}.button:hover{background-color:#ef5555}.button:active{box-shadow:#333 0 0 3px 0}

.users-wrapper{display:grid;grid-template-columns:1fr;grid-row-gap:16px;row-gap:16px;justify-items:left}.users{place-self:stretch}

.user-wrapper{display:grid;grid-template-columns:1fr;grid-row-gap:36px;row-gap:36px}.user__table{width:100%;border-collapse:collapse}.user__table th{padding:8px 6px;background-color:#666;color:#ffffff;font-weight:500;text-align:left}.user__table td{border-bottom:1px solid #bbbbbb;padding:8px 6px}.user__buttons-wrapper{display:flex;grid-gap:24px;gap:24px}

.user-posts__post-wrapper{display:grid;grid-template-columns:1fr;justify-items:left;grid-row-gap:36px;row-gap:36px}

